import React, { useCallback, useMemo } from 'react';
import {
  Grid,
  FormControlLabel,
  Checkbox,
  TextField,
  Radio,
  Button,
  RadioGroup,
  makeStyles,
  colors,
  Typography,
} from '@material-ui/core';
import { useForm } from 'react-hook-form';
import Section from 'components/Forms/MentoringApply/Section';
import Fieldset from 'components/Forms/MentoringApply/Fieldset';
import { MentoringApplyFormDto } from 'models/forms';
import { positions, Interests, WorkingLocation } from 'src/constants/applyForm';
import Loading from 'components/Utils/Loading';
import { MENTORING_EXPIRY, MENTORING_GROUP } from 'src/constants';
import { useCountDown } from 'src/hooks/useCountDown';
import Emoji from 'src/components/Utils/Emoji';

const useStyles = makeStyles(theme => ({
  root: {
    '& .MuiFormLabel-asterisk': {
      fontWeight: 'bold',
      color: theme.palette.primary.main,
    },
  },
  options: {
    borderRadius: theme.shape.borderRadius,
    marginRight: 7,
    paddingRight: 9,
    '&:hover': {
      backgroundColor: theme.palette.action.hover,
    },
  },
  expiredText: {
    textAlign: 'center',
    marginBottom: theme.spacing(3),
  },
}));

interface Props {
  onSubmit?: (data: MentoringApplyFormDto) => void;
  loading?: boolean;
}
const MentoringApplyForm = ({ onSubmit, loading = false }: Props) => {
  const styles = useStyles();
  const { register, handleSubmit, errors, watch } = useForm();
  const formValues = watch();
  const { expired } = useCountDown(MENTORING_EXPIRY);

  const checkedExtraInterest = useMemo(() => {
    return formValues.interestIndustry && formValues.interestIndustry.includes('기타');
  }, [formValues.interestIndustry]);

  const checkedExtraLocation = useMemo(() => {
    return formValues.workingLocations && formValues.workingLocations.includes('기타');
  }, [formValues.workingLocations]);

  const submitButtonText = useMemo(() => (expired ? '마감되었습니다' : '신청하기'), [expired]);

  const onSubmitHandler = useCallback(
    data => {
      const formData = { ...data };
      const { job, otherJob, applyReasons, otherReason, workingLocations, otherLocation } = formData;
      if (job === '기타') {
        formData.job = otherJob;
        delete formData.otherJob;
      }
      if (checkedExtraInterest) {
        const index = applyReasons.findIndex((reason: Interests) => reason === '기타');
        formData.applyReasons[index] = otherReason;
        delete formData.otherReason;
      }

      if (checkedExtraLocation) {
        const index = workingLocations.findIndex((location: WorkingLocation) => location === '기타');
        formData.workingLocations[index] = otherLocation;
        delete formData.otherLocation;
      }

      onSubmit?.(formData);
    },
    [checkedExtraInterest, checkedExtraLocation, onSubmit]
  );

  const questionsByPosition = useMemo(() => {
    switch (formValues.position) {
      case '웹 프론트엔드 개발자':
        return 프론트질문리스트;
      case '백엔드 개발자':
        return 백엔드질문리스트;
      case 'UI/UX 디자이너':
        return 디자인질문리스트;
      default:
        return [];
    }
  }, [formValues.position]);

  if (expired) {
    return (
      <>
        <Typography className={styles.expiredText} gutterBottom>
          {`멘토링 프로젝트 ${MENTORING_GROUP}기 신청이 마감되었습니다.`}
          <br />
          다음 기회에 다시 신청해주세요! <Emoji emoji="🙇‍♂️" name="thanks" />
        </Typography>
        <Button href="/" color="primary" variant="contained" fullWidth>
          홈으로
        </Button>
      </>
    );
  }

  return (
    <Grid
      component="form"
      container
      noValidate
      autoComplete="off"
      onSubmit={(e: any) => e.preventDefault()}
      className={styles.root}
    >
      <Section title="연락에 필요한 기본정보를 작성해주세요.">
        <Fieldset label="이메일" required={true} error={Boolean(errors.email)}>
          <TextField
            name="email"
            type="email"
            placeholder="lubycon@gmail.com"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '이메일을 입력해주세요',
              },
              pattern: {
                value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/,
                message: '올바른 이메일을 입력해주세요',
              },
            })}
            error={Boolean(errors.email)}
            helperText={errors.email?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="이름" required={true} error={Boolean(errors.name)}>
          <TextField
            name="name"
            placeholder="홍길동"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '이름을 입력해주세요',
              },
              pattern: {
                value: /[a-zA-Z가-힣]+/,
                message: '올바른 이름을 입력해주세요',
              },
            })}
            error={Boolean(errors.name)}
            helperText={errors.name?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="휴대폰 번호 (-는 빼주세요)" required={true} error={Boolean(errors.phone)}>
          <TextField
            name="phone"
            type="tel"
            placeholder="01012341234"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '휴대폰 번호를 입력해주세요',
              },
              pattern: {
                value: /^01\d\d{4}\d{4}$/,
                message: '올바른 휴대폰 번호를 입력해주세요',
              },
            })}
            error={Boolean(errors.phone)}
            helperText={errors.phone?.message}
          />
        </Fieldset>
      </Section>
      <Section title="현재 무슨 일을 하고 계시나요?">
        <Fieldset label="포지션" required={true}>
          <RadioGroup row defaultValue={positions[0]}>
            {positions.map(position => (
              <FormControlLabel
                key={position}
                name="position"
                label={position}
                value={position}
                control={<Radio />}
                inputRef={register()}
                className={styles.options}
              />
            ))}
          </RadioGroup>
        </Fieldset>
        <Fieldset label="이 포지션의 경력이 어떻게 되시나요? (연차)" required={true}>
          <TextField name="careerYear" type="number" variant="outlined" inputRef={register()} />
        </Fieldset>
      </Section>
      <Section title="여러분을 알려주세요.">
        {필수질문리스트.map(question => (
          <Fieldset key={question.key} label={question.label} required={question.required}>
            <TextField multiline name={question.key} variant="outlined" inputRef={register()} />
          </Fieldset>
        ))}
      </Section>
      <Section title="직군별 질문">
        {questionsByPosition.map(question => (
          <Fieldset key={question.key} label={question.label} required={question.required}>
            <TextField multiline name={question.key} variant="outlined" inputRef={register()} />
          </Fieldset>
        ))}
      </Section>
      <Section title="선택 항목">
        <p style={{ margin: '-1rem 0px 2rem', color: 'rgba(0, 0, 0, 0.54)', fontStyle: 'italic' }}>
          처음 만나는 사람들과 팀을 이루고 짧은 시간 동안 하나의 서비스를 완성하는 것은 쉬운 일이 아닙니다. 여러분에
          대해 더 많이 알 수록 시너지를 낼 수 있는 더 좋은 팀을 꾸릴 수 있을 거예요. 가능한 많은 항목에 답변해주세요.
        </p>
        {선택질문리스트.map(question => (
          <Fieldset key={question.key} label={question.label} required={question.required}>
            <TextField multiline name={question.key} variant="outlined" inputRef={register()} />
          </Fieldset>
        ))}
        <Typography component="h3" variant="h6" style={{ margin: '0 0 0.6rem' }}>
          포트폴리오/깃헙/블로그 링크 (선택)
        </Typography>
        <p style={{ margin: '-0.2rem 0px 2rem', color: 'rgba(0, 0, 0, 0.54)', fontStyle: 'italic' }}>
          파일인 경우에는 lubycon@gmail.com으로 보내주시기 바랍니다
        </p>
        <Fieldset label={'링크 1'} required={false}>
          <TextField type="url" name="link1" variant="outlined" inputRef={register()} />
        </Fieldset>
        <Fieldset label={'링크 2'} required={false}>
          <TextField type="url" name="link2" variant="outlined" inputRef={register()} />
        </Fieldset>
        <Fieldset label={'링크 3'} required={false}>
          <TextField type="url" name="link3" variant="outlined" inputRef={register()} />
        </Fieldset>
      </Section>

      <Section>
        <Fieldset
          label="인재풀 확보에 동의해주신다면, 만약 멘토링 프로젝트에서 떨어지더라도 프로젝트 중간에 인원 충원이 필요할
            경우에 가장 먼저 연락을 드릴 수 있습니다!"
        >
          <FormControlLabel
            name="agreeUsePrivacyData"
            label="동의합니다"
            inputRef={register()}
            control={<Checkbox />}
          />
        </Fieldset>
        <Button
          color="primary"
          size="large"
          variant="contained"
          fullWidth={true}
          onClick={handleSubmit(onSubmitHandler)}
          disabled={loading || expired}
        >
          {loading ? <Loading size={30} color={colors.grey['800']} /> : submitButtonText}
        </Button>
      </Section>
    </Grid>
  );
};

export default MentoringApplyForm;

const 필수질문리스트: 질문[] = [
  {
    key: 'wantGet',
    label: '이 멘토링을 통해 얻어가고 싶은 게 무엇인가요?',
    required: true,
  },
  {
    key: 'happyMoment',
    label: '개발자/디자이너를 하면서 가장 즐거웠던 기억은 무엇이었나요?',
    required: true,
  },
  {
    key: 'concernPoint',
    label:
      '멘토가 미리 알고있으면 좋을만한 걱정거리가 있나요? (e.g. 내가 OO하지 못할까봐 걱정된다. OO한 점은 없었으면 좋겠다)',
    required: true,
  },
];
